const status = {
  PRE_BUSSINESS_AUDIT: "PRE_BUSSINESS_AUDIT",
  AUDIT_TRUE: "AUDIT_TRUE",
  AUDIT_FALSE: "AUDIT_FALSE",
  get: s => {
    return s === "PRE_BUSSINESS_AUDIT" ? "" :
      s === "AUDIT_TRUE" ? "" :
        s === "AUDIT_FALSE" ? "" :
          "--";
  }
};

const allStatus = {
  SUBMIT: "SUBMIT",
  PRE_PAY: "PRE_PAY",
  PRE_PARENT_AUDIT: "PRE_PARENT_AUDIT",
  PRE_BUSSINESS_AUDIT: "PRE_BUSSINESS_AUDIT",
  PRE_SYS_AUDIT: "PRE_SYS_AUDIT",
  AUDIT_TRUE: "AUDIT_TRUE",
  AUDIT_FALSE: "AUDIT_FALSE",
  FINISH: "FINISH",
  CANCLE: "CANCLE",
  DEL: "DEL",
  get: s => {
    return s === "SUBMIT" ? "" :
      s === "PRE_PAY" ? "" :
        s === "PRE_PARENT_AUDIT" ? "" :
          s === "PRE_BUSSINESS_AUDIT" ? "" :
            s === "PRE_SYS_AUDIT" ? "" :
              s === "AUDIT_TRUE" ? "" :
                s === "AUDIT_FALSE" ? "" :
                  s === "FINISH" ? "" :
                    s === "CANCLE" ? "" :
                      s === "DEL" ? "" : "--";
  }
};

